/*--------------------------------------------------------------
# Embedded Fonts
--------------------------------------------------------------*/
/**
 * Sample @font-face rules for Merriweather
 * 
 * Usage:
 * Copy these rules into the appropriate Sass partial and update the file paths as needed
 *
*/

@font-face {
    font-family: 'Merriweather Web';
    src: url('fonts/merriweather-light-webfont.eot');
    src: url('fonts/merriweather-light-webfont.eot?#iefix') format('embedded-opentype'),
         url('fonts/merriweather-light-webfont.woff2') format('woff2'),
         url('fonts/merriweather-light-webfont.woff') format('woff'),
         url('fonts/merriweather-light-webfont.ttf') format('truetype'),
         url('fonts/merriweather-light-webfont.svg#merriweatherlight') format('svg');
    font-weight: normal;
    font-style: normal;
  }
  
  @font-face {
    font-family: 'Merriweather Web';
    src: url('fonts/merriweather-lightitalic-webfont.eot');
    src: url('fonts/merriweather-lightitalic-webfont.eot?#iefix') format('embedded-opentype'),
         url('fonts/merriweather-lightitalic-webfont.woff2') format('woff2'),
         url('fonts/merriweather-lightitalic-webfont.woff') format('woff'),
         url('fonts/merriweather-lightitalic-webfont.ttf') format('truetype'),
         url('fonts/merriweather-lightitalic-webfont.svg#merriweatherlight_italic') format('svg');
    font-weight: normal;
    font-style: italic;
  }
  
  @font-face {
    font-family: 'Merriweather Web';
    src: url('fonts/merriweather-bold-webfont.eot');
    src: url('fonts/merriweather-bold-webfont.eot?#iefix') format('embedded-opentype'),
         url('fonts/merriweather-bold-webfont.woff2') format('woff2'),
         url('fonts/merriweather-bold-webfont.woff') format('woff'),
         url('fonts/merriweather-bold-webfont.ttf') format('truetype'),
         url('fonts/merriweather-bold-webfont.svg#merriweatherbold') format('svg');
    font-weight: bold;
    font-style: normal;
  }
  
  @font-face {
    font-family: 'Merriweather Web';
    src: url('../fonts/Merriweather/fonts/merriweather-bolditalic-webfont.eot');
    src: url('../fonts/Merriweather/fonts/fonts/merriweather-bolditalic-webfont.eot?#iefix') format('embedded-opentype'),
         url('../fonts/Merriweather/fonts/fonts/merriweather-bolditalic-webfont.woff2') format('woff2'),
         url('../fonts/Merriweather/fonts/fonts/merriweather-bolditalic-webfont.woff') format('woff'),
         url('../fonts/Merriweather/fonts/fonts/merriweather-bolditalic-webfont.ttf') format('truetype'),
         url('../fonts/Merriweather/fonts/fonts/merriweather-bolditalic-webfont.svg#merriweatherbold_italic') format('svg');
    font-weight: bold;
    font-style: italic;
  }
